import React, { Component } from "react";
import { StaticQuery, graphql } from "gatsby";
import Navbar from '../components/navbar';
import Contact from '../components/contact';
import Footer from '../components/footer';

export default function Policy() {
  return (
    <StaticQuery
      query={graphql`
        query Policy {
          allWpPage(filter: { slug: { eq: "privacy-policy-2" } }) {
            nodes {
              content
            }
          }
        }
      `}
      render={(data) => (
        <><Navbar /><section
          id="post-200"
          class="post-200 page type-page status-publish hentry"
        >
          <header
            class="entry-header text-center"
            style={{ marginTop: "100px" }}
          >
            <h1 class="entry-title">Privacy Policy</h1>
          </header>
          <div class="entry-content">
            <div class="container">
              <div class="row">
                <div class="col-sm-8 m-auto" style={{ padding: "0 35px" }}>
                  <div
                    dangerouslySetInnerHTML={{
                      __html: data.allWpPage.nodes[0].content,
                    }} />
                </div>
              </div>
            </div>
          </div>
        </section><Contact/><Footer /></>
      )}
    />
  );
}
